import React, { Component } from 'react';
import { autorun } from "mobx"
import Victor from "victor"
import { teams } from "../lib/gameInfo"

function rect(props) {
    const {ctx, x, y, width, height} = props;
    ctx.fillRect(x, y, width, height);
}

class CanvasThings extends Component {
    componentDidMount() {
        this.componentDidUpdate()
    }

    componentDidUpdate() {
        // clear existing autorun
        if (this.disposer) this.disposer();
        // update bullet positions when mobx values are updated
        this.disposer = autorun(() => {
            const ctx = this.refs.canvas.getContext('2d');
            ctx.fillStyle = "white"
            ctx.clearRect(0, 0, this.props.width, this.props.height);

            // if(this.props.bullets.length === 0) return;

            //render each line
            this.props.units.forEach(unit => {
                const { targetCoordinates, x, y, team: { colour } } = unit
                if (targetCoordinates) {
                    let offset = unit.team === teams.red ? 3 : -3
                    ctx.strokeStyle = colour;
                    ctx.beginPath()
                    ctx.setLineDash([2, 5])
                    ctx.moveTo(x + offset, y + offset)
                    ctx.lineTo(targetCoordinates.x + offset, targetCoordinates.y + offset)
                    ctx.lineWidth = unit.showDistanceRings ? 10 : 1;
                    ctx.stroke()
                }
            })

            // render each bullet
            this.props.bullets.forEach(b => {
                const { sourceCoordinate: { x: sourceX, y: sourceY }, currentCoordinate: { x, y } } = b
                let angle = (new Victor(x-sourceX, sourceY - y)).verticalAngle()
                ctx.fillStyle = b.sourceUnit.team.bulletColour
                ctx.translate(x, y)
                ctx.rotate(angle);
                rect({ ctx, x: -1.5, y: -3, width: 3, height: 7 });
                ctx.setTransform(1, 0, 0, 1, 0, 0);
            })
        })
    }

    render() {
        const { width, height } = this.props
        return (
            <canvas ref="canvas" className="canvas" width={width} height={height} style={{ position: "absolute", top: 0, left: 0}}></canvas>
        );
    }
}

export default CanvasThings


